import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import { FormGroup, Input } from '../../../node_modules/@depack/form/build/index.js'

/**
 * The image for the form that can be changed.
 */
export default class FormImage extends Component {
  constructor() {
    super()
    this.state = { resetImage: false }
  }
  render({ editing, required, image, help }) {
    const { resetImage } = this.state
    if (editing && !resetImage) return h(FormGroup,{help:help,label:"Изображение"},
      h('br'),
      h('img',{'src':image,'className':"img-fluid"}),
      h('a',{'onClick':(e) => {
        e.preventDefault()
        this.setState({ resetImage: true })
        return false
      },'href':"#",'className':"btn btn-outline-warning"},`Изменить`),
    )
    if(!editing || resetImage) return           h(FormGroup,{help:help,label:"Изображение"},
      h(Input,{required:required !== undefined,name:"image",type:"file",file:"1"}),
    )
  }
}