import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import fetch from '../../../node_modules/unfetch/dist/unfetch.mjs'
import { Col } from './../../frontend/components/Bootstrap.jsx'
import ArticleEditor from './../ArticleEditor.jsx'
import Form, { FormGroup, Input, TextArea, Select } from '../../../node_modules/@depack/form/build/index.js'

export default class AddCategory extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      data: {},
      categories: [],
      hint: '1-комнатные-апартаменты-воскресенское',
      catSeo: 'апартаменты',
      article: '',
    }
  }
  async loadCategories() {
    this.setState({ loading: true })
    try {
      const res = await fetch(`/admin-data?categories`)
      const { error, data } = await res.json()
      if (error) this.setState({ error })
      else {
        const categories = data.map(({ _id, title }) => ({
          value: _id,
          title,
        }))
        this.setState({ categories })
      }
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  async componentDidMount() {
    await this.loadCategories()

    const editing = !!this.props.id
    if (!editing) return
    this.setState({ editing: 1, loading: true })

    try {
      const res = await fetch(`/admin-data?objects&id=${this.props.id}`)
      const { error, data } = await res.json()
      if (error) this.setState({ error })
      else {
        const [d] = data
        this.setState({
          data: d,
          hint: d.seo,
          catSeo: d.categorySeo,
          article: d.article,
        })
      }
    } catch ({ message: error }) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  async submit(e) {
    this.setState({ error: null })
    e.preventDefault()
    const data = new FormData(this.form)
    data.append('article', this.state.article)
    this.setState({ formLoading: true })
    try {
      const res = await fetch('/admin-data?objects', {
        method: 'POST',
        body: data,
      })
      const { error } = await res.json()
      if (error) this.setState({ error })
      else this.setState({ success: 1 })
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ formLoading: false })
    }
    return false
  }
  getHint() {
    return `Фраза для поисковой оптимизации, участвующая в адресах страниц, например, knedv.ru/${this.state.catSeo}/<strong>${this.state.hint}</strong>.`
  }
  render() {
    const hint = this.getHint()
    const { editing, resetImage, categories } = this.state
    if (this.state.loading) {
      return h(Col,{},
        h('h1',{},this.state.editing ? 'Редактировать' : 'Добавить',` Объект`),
        h('span',{'className':"echo-loader"},`Loading…`),
      )
    }
    return                               h(Col,{},
      h('h1',{},this.state.editing ? 'Редактировать' : 'Добавить',` Объект`),
      !(editing && this.state.loading) &&
      h(Form,{formRef:r => this.form = r,onSubmit:this.submit.bind(this)},
        h(FormGroup,{help:"Название для каталога недвижимости.",label:"Название"},
          h(Input,{required:true,name:"title",value:this.state.data.title,placeholder:"1к. апартаменты, 21 кв.м, п. Воскресенское"}),
        ),
        h(FormGroup,{help:hint,label:"СЕО Название"},
          h(Input,{required:true,name:"seo",value:this.state.data.seo,placeholder:"1-комнатные-апартаменты-воскресенское"}),
        ),
        h(FormGroup,{help:"Описание объекта.",label:"Описание"},
          h(TextArea,{rows:10,required:true,name:"description",placeholder:"Новый торгово-гостиничный Комплекс «Воскресенский» в п. Воскресенское, который исполнен в стиле 'современная классика', что придает проекту свою индивидуальность и привлекательность в целях инвестиций. В комплексе будут развиты свои сервисные службы, и он станет достойным торгово-гостиничным комплексом, который будет являться частью п.Воскресенское: д/о Воскресенское, ФГАО Оздоровительный Комплекс «Архангельское» (Управ делами Президента РФ), детская балетная школа, хореографическая школа, детский центр творчества, детский музыкальный театр, студия музыкального развития, п. Юрьев Сад (таунхаусы), п. Кронбург (квадрохаусы), дачи известных людей СССР и политических деятелей нашего времени. Выгодные инвестиции (сдача в аренду посуточно, месячно, годично)."},
            this.state.data.description,
          ),
        ),

        editing && !resetImage && h('div',{'className':"form-group"},
          h('label',{},`Изображение`),h('br'),
          h('img',{'src':this.state.data.cdnImage,'className':"img-fluid"}),
          h('a',{'onClick':(e) => {
            e.preventDefault()
            this.setState({ resetImage: true })
            return false
          },'href':"#",'className':"btn btn-outline-warning"},`Изменить`),
        )
        ,
        (!editing || resetImage) && h(FormGroup,{label:"Изображение",help:"Картинка, отображаемая на главной странице."},
          h(Input,{required:true,name:"image",file:"1",type:"file"}),
        ),
        h(ArticleEditor,{article:this.state.article,onSave:(html) => {
          this.setState({ article: html })
        }}),
        editing && h('input',{'value':this.props.id,'type':"hidden",'name':"id"}),
        h(FormGroup,{label:"Раздел",help:"Категория в каталоге"},
          h(Select,{options:categories,name:"category",required:true,value:this.state.data.category}),
        ),
        h('button',{'disabled':this.state.formLoading,'type':"submit",'className':"btn btn-primary"},this.state.formLoading ? 'Загрузка...' : `${editing ? 'Сохранить' : 'Добавить'}`),
        h(Error,{error:this.state.error}),
        this.state.success && h('div',{'className':"alert alert-success mt-3",'role':"alert"},`Объект успешно `,editing ? 'сохранен' : 'создан',`!`),
      )
      ,
    )
  }
}

const Error = ({ error }) => {
  if (!error) return null
  return h('div',{'className':"alert alert-danger mt-3",'role':"alert"},error)
}