import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import fetch from '../../../node_modules/unfetch/dist/unfetch.mjs'
import { Col, Icon, Switch } from './../../frontend/components/Bootstrap.jsx'
import DeleteModal, { EditModal } from './../DeleteModal.jsx'
import Form, { FormGroup, Input, TextArea } from '../../../node_modules/@depack/form/build/index.js'
import FormImage from './../Components/FormImage.jsx'
import SpecialForm from './../Components/SpecialForm.jsx'

export default class Special extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      data: [],
    }
  }
  async componentDidMount() {
    await this.load()
  }
  async load() {
    this.setState({ loading: true })
    try {
      /** @type {{ json: function(): Promise<{ error: string, data: Array<Special> }> }} */
      const res = await fetch('/admin-data?specials')
      const { error, data } = await res.json()
      if (error) this.setState({ error })
      else this.setState({ data })
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  openModal(modal) {
    this.setState({ modal })
  }
  openEdit(edit) {
    this.setState({ edit })
  }
  render() {
    const f =      h('details',{},
      h('summary',{},
        h('h3',{'style':"display: inline-block;vertical-align: middle;"},`Создать Новое Предложение`),
      ),
      h(SpecialsForm,{submitFinish:() => {
        this.load()
      },path:"/admin-data?specials",successMessage:"Предложение успешно создано!",confirmText:"Добавить"}),
    )
    return                                                                  h(Col,{},
      h('h1',{},`Специальные Предложения`),
      h(List,{data:this.state.data,loading:this.state.loading,openModal:this.openModal.bind(this),openEdit:this.openEdit.bind(this)}),
      h('hr'),
      f,
      this.state.modal &&
        h(DeleteModal,{...this.state.modal,onClose:this.openModal.bind(this, null),btnClass:"danger",onComplete:this.load.bind(this)})
      ,
      this.state.edit &&
        h(EditModal,{onClose:this.openEdit.bind(this, null),title:"Редактирование"},
          h(SpecialsForm,{item:this.state.edit,submitFinish:this.load.bind(this),onClose:this.openEdit.bind(this, null),path:"/admin-data?specials",closeText:"Отмена",successMessage:"Предложение успешно отредактировано!",confirmText:"Сохранить"}),
        )
      ,
    )
  }
}

const List = ({ data, openModal, openEdit, loading }) => {
  return (                           h('div',{'style':"height:25rem;overflow:scroll;background:wheat; padding:0.5rem;"},
    loading && h('span',{'className':"echo-loader"},`Loading…`),
    !loading && !data.length && 'Нет специальных предложений.',
    data.map((item) => {
      return h(Item,{item:item,key:item._id,openModal:openModal,openEdit:openEdit})
    }),
  ))
}


const Item = ({ item, openModal, openEdit }) => {
  const { _id, title, cdnImage, description, price, show_on_main } = item
  const shownOnMain = show_on_main == 'on'
  return (h('div',{'className':shownOnMain ? 'IsShownOnMain' : '','style':"border-bottom:1px solid brown;border-top:1px solid lightcoral;"},
    h('h4',{},title,` `,shownOnMain && h('span',{'className':"badge badge-danger"},`На главной`)),
    h('p',{},
      h('img',{'src':cdnImage,'style':"display:block;"}),
      description,
      h('span',{'style':"font-weight: bold;"},` `,price),
    ),
    h('a',{'onClick':(e) => {
      e.preventDefault()
      openModal({
        text: h('span',{},`Вы действительно хотите удалить предложение `,h('strong',{},title),`?`),
        confirmText: 'Удалить',
        title: 'Удаление Предложения',
        path: `specials&id=${_id}&delete`,
      })
      return false
    },'style':"color:brown;",'href':"#"},
      h(Icon,{icon:"far fa-trash-alt"}),
    ),
    h('a',{'onClick':(e) => {
      e.preventDefault()
      openEdit(item)
      return e
    },'style':"color:brown;",'href':"#"},
      h(Icon,{icon:"fas fa-pen"}),
    ),
  ))
}

class SpecialsForm extends SpecialForm {
  render({ item, onClose, closeText = 'Отмена', successMessage, confirmText = 'Добавить' }) {
    const i = item || {}
    const { formLoading } = this.state
    return (                                                    h(Form,{onSubmit:this.submit.bind(this),onChange:() => {
      this.setState({ error: null, success: null })
    }},
      h(FormGroup,{label:"Название",help:"Заголовок для главной страницы, напр., Ленинский проспект, дом 114"},
        h(Input,{value:i.title,placeholder:"Название акции",name:"title",required:1}),
      ),
      h(FormGroup,{label:"Описание",help:"Введите описание акции..."},
        h(TextArea,{required:true,name:"description",placeholder:"Описание акции"},i.description),
      ),

      h(FormImage,{editing:item,help:"Картинка, отображаемая на главной странице.",required:1,image:i.cdnImage}),

      h(FormGroup,{label:"Цена",help:"Задайте цену..."},
        h(Input,{value:i.price,name:"price",placeholder:"55 000 000 руб."}),
      ),

      h(FormGroup,{label:"Переход",help:"Ссылка на страницу каталога, или сайта."},
        h(Input,{value:i.href,name:"href",placeholder:"/каталог/москва-элитная/лениниский-проспект-дом-114"}),
      ),

      h(FormGroup,{help:"Добавить в специальные предложения на главной."},
        h(Switch,{value:i.show_on_main,label:"Отображать на главной",name:"show_on_main"}),
      ),

      item && h('input',{'value':i._id,'type':"hidden",'name':"id"}),

      h(Error,{error:this.state.error}),
      h(Success,{success:this.state.success,message:successMessage}),

      h('button',{'disabled':formLoading,'type':"submit",'className':"btn btn-primary"},formLoading ? 'Загрузка...' : confirmText),

      onClose &&
        h('button',{'onClick':onClose,'type':"button",'className':"FormCancelBtn btn btn-secondary"},closeText)
      ,
    ))
  }
}

const Error = ({ error }) => {
  if (!error) return null
  return (h('div',{'className':"alert alert-danger mt-3",'role':"alert"},error))
}

const Success = ({ success, message }) => {
  if (!success) return null
  return (h('div',{'className':"alert alert-success mt-3",'role':"alert"},message || success))
}